      i += num - 1;
    };
  
  },
  methods: {
    toChinesNum(num) {
      let changeNum = [
        "零",
        "一",
        "二",
        "三",
        "四",
        "五",
        "六",
        "七",
        "八",
        "九"
      ]; //changeNum[0] = "零"
      let unit = ["", "十", "百", "千", "万"];
      num = parseInt(num);
      let getWan = temp => {
       let bool=false;
        if(temp>=10 && temp<20){
           bool=true;
        }
        let strArr = temp
          .toString()
          .split("")
          .reverse();
        let newNum = "";
        
        for (var i = 0; i < strArr.length; i++) {
          newNum =(i == 0 && strArr[i] == 0? "": i > 0 && strArr[i] == 0 && strArr[i - 1] == 0? "": changeNum[strArr[i]] + (strArr[i] == 0 ? unit[0] : unit[i])) + newNum;
        }
        if(bool){
          let arr= newNum.split("");
          let str ="";
          for(let i =0;i<arr.length;i++){
            if(i>0){
              str=str+arr[i]
            }
          }
           return str;
        }
        return newNum;
      };
      let overWan = Math.floor(num / 10000);
      let noWan = num % 10000;
      if (noWan.toString().length < 4) noWan = "0" + noWan;
      return overWan ? getWan(overWan) + "万" + getWan(noWan) : getWan(num);
    },

    indexMethod(index) {
      return index + 1;
    },
    adda(inedx, prop, key, arr) {
      let name = arr[inedx][prop];
      let row = 0;
      for (let i = inedx; i < arr.length; i++) {
        if (name == arr[i][prop]) {
          row++;
        } else {
          break;
        }
      }
      arr[inedx][key] = row;
      return row;
    },
    closess(done) {
      done();
    },
    search() {},
    //选择新增类型
    typeClick(item) {
      if (item.typeName == "巡检维护") {
