        this.visiDialogF = true;
      } else if (item.typeName == "仪器设备检修") {
        this.visiDialogE = true;
      } else if (item.typeName == "耗材及配件更换") {
        this.visiDialogG = true;
      } else if (item.typeName == "仪器参数变更") {
        this.visiDialogH = true;
